@tailwind base;
@tailwind components;
@tailwind utilities;

body {
    @apply font-sans bg-white dark:bg-darkBackground text-gray-900 dark:text-blue-50;
}

.cta {
    @apply fill-current opacity-50 transition duration-200;
}

.cta:hover {
    @apply opacity-100;
}

button:focus {
    outline: thin dotted;
}

@import "./components/Snap/Snap.css";

.skill-slide__progress-bar {
    @apply transition opacity-0;
}

.skill-slide:focus .skill-slide__progress-bar,
.skill-slide:hover .skill-slide__progress-bar {
    @apply opacity-100;
}

.skill-slide:focus .skill-slide__ar {
    @apply scale-110;
}

.timeline-chip {
    @apply relative;
}

.timeline-chip::before,
.timeline-chip::after {
    @apply hidden absolute text-center text-lg font-semibold;
    content: attr(data-text);
}

.timeline-chip--left::before {
    @apply block right-8 p-4;
}

.timeline-chip--right::after {
    @apply block left-8 p-4;
}

.timeline-row {
    @apply flex justify-between items-center w-full;
}
.timeline-row--left {
    @apply flex-row-reverse;
}

.clamp {
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
}

.map-ratio {
    transition: 1s clip-path ease-in-out;
    animation: circleExpand 1s ease-in 0.5s 1 normal forwards;
    clip-path: circle(0%);
}

@keyframes circleExpand {
    from {
        clip-path: circle(0%);
    }

    to {
        clip-path: circle(100%);
    }
}
